import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import ReactTooltip from "react-tooltip";
import { Field, formValueSelector, reduxForm, reset } from "redux-form";
import { getcustomerList } from "../actions/customerAction";
import { getKelompokHarga } from "../actions/kelompok_harga";
import { getListBarang } from "../actions/kodeBarangAction";
import { berhasil, gagal } from "../Alert";
import { AxiosMasterPost, AxiosMasterPut } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { ToastInfo } from "../GlobalComponent/notification";
import HeaderCard from "../HeaderCard";
import { NormalizeNumberOnly } from "../helper";
import { renderField, renderFieldSelect } from "../renderField";
import { createNumberMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class KelompokHarga extends Component {
  constructor(props) {
    super(props);
    this.state = {
      edit: false,
      columns: [
        {
          dataField: "nama_toko",
          text: "Nama Toko",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "nama_jenis",
          text: "Model Barang",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kode_tukar_normal",
          text: "Normal",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "return_normal",
          text: "Return Normal",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kode_tukar_set",
          text: "Set",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "6%" };
          },
        },
        {
          dataField: "return_rongsok",
          text: "Return Rongsok",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "ongkos_reparasi",
          text: "Reparasi",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "harga_aksesoris",
          text: "Aksesoris",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "11%" };
          },
        },
        {
          dataField: "diskon",
          text: "Diskon",
          align: "right",
          headerAlign: "right",
          headerStyle: () => {
            return { width: "7%" };
          },
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center row-justify-center">
                <button
                  data-tip
                  data-for="edit"
                  className="btn btn-success mt-1"
                  onClick={() => this.duplikat(row)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-clone mr-1"></i>
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Duplikat</span>
                  </ReactTooltip>
                  Duplikat
                </button>
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-warning mt-1 ml-2"
                  onClick={() => this.edit(row)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-edit "></i>
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  Edit
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  duplikat(data) {
    this.props.change("kode_tukar_normal", data.kode_tukar_normal);
    this.props.change("kode_tukar_set", data.kode_tukar_set);
    this.props.change("ongkos_reparasi", data.ongkos_reparasi);
    this.props.change("kode_jenis", data.kode_jenis);
    this.props.change("return_normal", data.return_normal);
    this.props.change("return_rongsok", data.return_rongsok);
    this.props.change("harga_aksesoris", data.harga_aksesoris);
    ToastInfo("Duplikasi berhasil");
  }

  edit(data) {
    this.props.change("kode_tukar_normal", data.kode_tukar_normal);
    this.props.change("kode_tukar_set", data.kode_tukar_set);
    this.props.change("ongkos_reparasi", data.ongkos_reparasi);
    this.props.change("kode_jenis", data.kode_jenis);
    this.props.change("nama_toko", data.kode_customer);
    this.props.change("return_normal", data.return_normal);
    this.props.change("return_rongsok", data.return_rongsok);
    this.props.change("harga_aksesoris", data.harga_aksesoris);
    this.setState({
      edit: true,
    });
  }
  componentDidMount() {
    this.props.dispatch(getKelompokHarga());
    this.props.dispatch(getListBarang());
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getListBarang());
  }
  simpanKelompok() {
    let data = {
      kode_customer: this.props.nama_toko,
      kode_jenis: this.props.kode_jenis,
      kode_tukar_normal: this.props.kode_tukar_normal,
      kode_tukar_set: this.props.kode_tukar_set,
      ongkos_reparasi: this.props.ongkos_reparasi,
      return_normal: this.props.return_normal,
      return_rongsok: this.props.return_rongsok,
      harga_aksesoris: this.props.harga_aksesoris,
      diskon: this.props.diskon,
    };
    let dataEdit = {
      kode_tukar_normal: this.props.kode_tukar_normal,
      kode_tukar_set: this.props.kode_tukar_set,
      ongkos_reparasi: this.props.ongkos_reparasi,
      return_normal: this.props.return_normal,
      return_rongsok: this.props.return_rongsok,
      harga_aksesoris: this.props.harga_aksesoris,
      diskon: this.props.diskon,
    };
    console.log(JSON.stringify(data));
    !this.state.edit
      ? AxiosMasterPost("kelompok-harga/add", data)
          .then(() => berhasil("Berhasil Disimpan"))
          .then(() => this.props.dispatch(reset("KelompokHarga")))
          .then(() => this.props.dispatch(getKelompokHarga()))
          .then(() =>
            this.setState({
              edit: false,
            })
          )
          .catch((err) =>
            gagal(`Gagal Menyimpan Data, Detail : ${err.response.data}`)
          )
      : AxiosMasterPut(
          `kelompok-harga/edit/${this.props.nama_toko}&${this.props.kode_jenis}`,
          dataEdit
        )
          .then(() => berhasil("Berhasil Disimpan"))
          .then(() => this.props.dispatch(reset("KelompokHarga")))
          .then(() => this.props.dispatch(getKelompokHarga()))
          .catch((err) =>
            gagal(`Gagal Menyimpan Data, Detail : ${err.response.data}`)
          );
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <ol className="breadcrumb mb-2">
          <Link to="/KlasifikasiBarang">
            <button type="button" className="btn btn-secondary ">
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          {this.kelompokHargaBaru()}
          <div className="col-lg-9">
            <div className="card">
              <HeaderCard title="Kelompok Harga" />
              <div className="card-body">
                <GlobalTabel
                  keyField="id"
                  data={this.props.KelompokHarga}
                  columns={this.state.columns}
                  search
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  kelompokHarga() {
    return (
      <div className="col-lg-4">
        <div className="card">
          <div className="card-body">
            <div className="col-lg-12">
              <h2>Tambah Kelompok Harga</h2>
            </div>
            <div className="col-lg-12">
              <hr />
            </div>
            <div className="col-lg-12">
              <Field
                name="kode_jenis"
                component={renderFieldSelect}
                options={this.props.kodeBarang.map((data) => {
                  let hasil = {
                    value: data.kode_jenis,
                    name: data.nama_jenis,
                  };
                  return hasil;
                })}
                type="text"
                label="Kode Jenis"
                placeholder="Masukan Kode Jenis"
                onChange={(data) => this.setKadarJual(data)}
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="kadar_jual"
                component={renderField}
                type="number"
                label="Harga Kode Tukar"
                placeholder="Masukan Harga Kode Tukar"
                readOnly
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="level_2"
                component={renderField}
                type="number"
                label="Harga Level 2"
                placeholder="Masukan Harga Level 2"
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="level_3"
                component={renderField}
                type="number"
                label="Harga Level 3"
                placeholder="Masukan Harga Level 3"
              />
            </div>
            <div className="col-lg-12 mt-2">
              <button
                className="btn btn-primary btn-block"
                onClick={() => this.simpanKelompok()}
              >
                SIMPAN
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  kelompokHargaBaru() {
    return (
      <div className="col-lg-3">
        <div className="card">
          <div className="card-body">
            <div className="col-lg-12 text-center">
              <h2>Tambah Kelompok Harga</h2>
            </div>
            <div className="col-lg-12">
              <hr />
            </div>
            <div className="col-lg-12">
              <Field
                name="nama_toko"
                component={renderFieldSelect}
                options={this.props.kodeCustomer.map((data) => {
                  let hasil = {
                    value: data.kode_customer,
                    name: data.nama_toko,
                  };
                  return hasil;
                })}
                type="text"
                label="Nama Toko"
                placeholder="Masukan Nama Toko"
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="kode_jenis"
                component={renderFieldSelect}
                options={this.props.kodeBarang.map((data) => {
                  let hasil = {
                    value: data.kode_jenis,
                    name: data.nama_jenis,
                  };
                  return hasil;
                })}
                label="Model Barang"
                placeholder="Masukan Model Barang"
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="kode_tukar_normal"
                component={renderField}
                normalize={NormalizeNumberOnly}
                label="Kode Tukar Normal"
                placeholder="Masukan Kode Tukar Normal"
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="kode_tukar_set"
                component={renderField}
                normalize={NormalizeNumberOnly}
                label="Kode Tukar Set"
                placeholder="Masukan Kode Tukar Set"
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="return_normal"
                component={renderField}
                normalize={NormalizeNumberOnly}
                label="Return Normal"
                placeholder="Masukan Return Normal"
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="return_rongsok"
                component={renderField}
                normalize={NormalizeNumberOnly}
                label="Return Rongsok"
                placeholder="Masukan Return Rongsok"
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="ongkos_reparasi"
                component={renderField}
                normalize={NormalizeNumberOnly}
                label="Ongkos Reparasi"
                placeholder="Masukan Ongkos Reparasi"
                {...currencyMask}
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="harga_aksesoris"
                component={renderField}
                normalize={NormalizeNumberOnly}
                label="Harga Aksesoris"
                placeholder="Masukan Harga Aksesoris"
                {...currencyMask}
              />
            </div>
            <div className="col-lg-12">
              <Field
                name="diskon"
                component={renderField}
                normalize={NormalizeNumberOnly}
                label="Diskon %"
                placeholder="Masukan Diskon"
              />
            </div>
            <div className="col-lg-12 mt-3">
              <button
                className="btn btn-primary btn-block"
                onClick={() => this.simpanKelompok()}
              >
                Selesai
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

KelompokHarga = reduxForm({
  form: "KelompokHarga",
})(KelompokHarga);
const selector = formValueSelector("KelompokHarga");
export default connect((state) => {
  const {
    kode_jenis,
    nama_toko,
    kode_tukar_normal,
    kode_tukar_set,
    ongkos_reparasi,
    return_rongsok,
    return_normal,
    harga_aksesoris,
    diskon,
  } = selector(
    state,
    "nama_toko",
    "kode_tukar_normal",
    "kode_tukar_set",
    "ongkos_reparasi",
    "kode_jenis",
    "harga_aksesoris",
    "return_normal",
    "return_rongsok",
    "diskon"
  );
  return {
    KelompokHarga: state.provinsi.kelompokHarga,
    kodeBarang: state.provinsi.kodeBarang,
    kodeCustomer: state.provinsi.customer,
    kode_jenis: kode_jenis,
    kode_tukar_normal: kode_tukar_normal,
    kode_tukar_set: kode_tukar_set,
    ongkos_reparasi: ongkos_reparasi,
    nama_toko: nama_toko,
    harga_aksesoris: harga_aksesoris,
    return_normal: return_normal,
    return_rongsok: return_rongsok,
    diskon: diskon,
  };
})(KelompokHarga);
